@import 'partials/_variables';
@import 'partials/_mixin';

/**
 * @type page
 * @name page-story
**/
.page-body.page-story {
	#local {
		.local-head {
			&::before {
				background-image: url(../images/story/main_bg_01.png);
			}
		}

		.content-body {
			.section.module-a {
				@include section-padding(top);
				@include section-padding(bottom);
		
				.section-wrap {
					.section-head {
						margin: 0 0 48rem 0;
		
						//laptop
						@include respond-above(large-1x) {
							margin: 0 0 56rem 0;
						}
					}
		
					.section-body {
						.posts-display {
							.button-display {
								margin-top: 48rem;
		
								//laptop
								@include respond-above(large-1x) {
									margin-top: 64rem; 
								}

								.button-area {
									.btn {
										max-width: 140rem;

										&:hover {
											transition: background-color 0.25s;
											background-color: color(normal, 010);

											.btn-text {
												transition: color 0.25s;
												@include font((weight: semi-bold)...);
												color: color(normal, 000);

	
												&::after {
													transition: background-image 0.25s;
													background-color: color(normal, 000);
												}
											}
										}
									}
								}
							}
							
							.posts-list {
								display: flex;
								flex-direction: column;
								gap: 32rem;
		
								//laptop
								@include respond-above(large-1x) {
									flex-direction: row;
									flex-wrap: wrap;
								}
		
								.posts-item {
									//laptop
									@include respond-above(large-1x) {
										width: calc(33.33% - (64rem / 3));
									}
									.posts-wrap {
										.posts-inform {
											.posts-data {
												//laptop
												@include respond-above(large-1x) {
													margin-top: 6rem;
												}
											}
										}
									}
								}
							}
						}
					}
				}
			}
		}
	}	
}


/**
 * @type page
 * @name page-story
**/
.page-body.page-story.page-intro {
	.content-body {
		.section {
			position: relative;
			@include section-padding(top);
			@include section-padding(bottom);

			&.news {
				&::before {
					content: '';
					display: block;
					position: absolute;
					width: 100vw;
					left: calc(50% - 50vw);
					top: 0;
					bottom: 0;
					right: 0;
					background-color: color(base, 005);
					z-index: -10;
				}
			}

			&.report {
				&::before {
					content: '';
					display: block;
					position: absolute;
					width: 100vw;
					left: calc(50% - 50vw);
					top: 0;
					bottom: 0;
					right: 0;
					background-color: color(accent, 020);
					z-index: -10;
				}

				.section-wrap {
					.section-head {
						.section-subject,
						.section-summary {
							color: color(base, 000);
						}
					}

					.section-body {
						.posts-display {
							.posts-list {
								.posts-item {
									.posts-wrap {
										.posts-inform {
											.posts-head {
												.posts-subject {
													color: color(base, 000);
												}
											}
											.posts-data {
												.data-list {
													.data-item {
														.body {
															color: color(base, 000);
														}
													}
												}
											}
										}
									}
								}
							}
							.button-display {
								.button-area {
									.btn {
										color: color(base, 000);
										border-color: color(base, 000);
										background-color: transparent;
		
										.btn-text {
											&::after,
											&::before {
												background-color: color(base, 000);
											}
										}

										&:hover {
											border-color: color(base, 100);
										}
									}
								}
							}
						}
					}
				}
			}
		}
	}
}

/**
 * @type page
 * @name page-story
**/
.page-body.page-story.page-newsletter {
	.content-body {
		.posts-read.module-a {
			@include section-padding(top);
			@include section-padding(bottom);
		}
	}
}

/**
 * @type page
 * @name page-story
**/
.page-body.page-story.page-awards {
	.content-body {
		.section.awards {
			.section-wrap {
				.section-body {
					.subsection {
						+.subsection {
							margin-top: 64rem;
						}

						.subsection-wrap {
							.subsection-head {
								.subsection-subject {
									@include font ((outline: body, size: small, weight: semi-bold)...);
									margin: 48rem 0 24rem 0;

									//laptop
									@include respond-above(large-1x) {
										margin: 80rem 0 32rem 0;
										@include font ((outline: head, size: large)...);
									}
								}
							}
							.subsection-body {
								>.data-display {

									>.data-list {
										display: flex;
										flex-direction: column;
										gap: 24rem;
									}

									.button-display {
										margin-top: 48rem;

										//laptop
										@include respond-above(large-1x) {
											margin-top: 80rem;
										}
									}
								}
							}
						}

						&:nth-child(1) {
							.subsection-body {
								>.data-display.module-d {
									>.data-list {
										// laptop
										@include respond-above(large-1x) {
											display: grid;
											gap: 32rem;
											grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr;
	
											.data-item:nth-child(1) {
												grid-column: 1 / span 3;
												grid-row: 1 / span 2;
											}

											.data-item:nth-child(2) {
												grid-column: 4 / span 3;
												grid-row: 1 / span 1;
											}

											.data-item:nth-child(3) {
												grid-column: 4 / span 3;
												grid-row: 2 / span 1;
											}

											.data-item:nth-child(4) {
												grid-column: 1 / span 2;
												grid-row: 3 / span 1;
											}
											.data-item:nth-child(5) {
												grid-column: 3 / span 2;
												grid-row: 3 / span 1;
											}

											&::after {
												content: '';
												display: block;
												grid-column: 5 / span 2;
												grid-row: 3 / span 1;
												border-radius: 20rem;
												background: color(base, 005) url(./../images/story/awards_figure.png) no-repeat center / 100% auto;
											}
										}
									}
								}
							}
						}

						&:nth-child(2) {
							.subsection-body {
								>.data-display.module-d {
									>.data-list {
										// tablet
										@include respond-above(large) {
											display: grid;
											gap: 24rem;
											grid-template-columns: 1fr 1fr;
										}

										// laptop
										@include respond-above(large-1x) {
											gap: 32rem;
											grid-template-columns: 1fr 1fr 1fr;
										}
									}
								}
							}
						}
					}
				}
			}
		}
	}
}