@import 'partials/_variables';
@import 'partials/_mixin';


/**
 * @type page
 * @name page-about
**/
.page-body.page-about {
	#local {
		.local-head {
			&::before {
				background-image: url(../images/about/main_bg_01.png);
			}
		}
	}

	.content-body {
		.section.misson-vision {
			@include section-padding(top);
			@include section-padding(bottom);
			
			.section-wrap {
				.section-head {
					@include hidden;
				}
				.section-body {
					display: flex;
					flex-direction: column;
					row-gap: 40rem;

					@include respond-above(large-1x) {
						flex-direction: row;
					}

					.subsection.module-a {
						
						@include respond-above(large-1x) {
							width: 50%;	
						}
					}
				}
			}
		}

		.section.philosophy {
			padding-top: 0;
			padding-bottom: 0;

			.section-wrap {
				.section-head {
					@include hidden;
				}
				.section-body {
					.subsection.module-a.value {
						position: relative;
						@include section-padding(top);
						@include section-padding(bottom);

						&::before {
							content: '';
							display: block;
							position: absolute;
							width: 100vw;
							left: calc(50% - 50vw);
							top: 0;
							bottom: 0;
							right: 0;
							background-color: color(base, 005);
							z-index: -1;
							background-image: url(../images/about/value_01.png);
							background-repeat: no-repeat;
							background-position: right bottom;
							background-size: auto;

							 //mobile
							@include respond-below(large) {
								background-size: 340rem 336rem;
							}
						}

						.subsection-wrap {
							.subsection-body {
								.data-display.module-a{
									margin-top: 56rem;

									.data-list {
										display: flex;
										flex-wrap: wrap;
										gap: 16rem;

										//laptop
										@include respond-above(large-1x) {
											column-gap: 32rem;
											flex-wrap: nowrap;
										}

										.data-item {
											//mobile
											@include respond-below(large-1x) {
												width: calc((100% /2) - 8rem);
											}
											
											//laptop
											@include respond-above(large-1x) {
												width: calc((100% /4) - 16rem);
											}

											&:nth-child(1) {
												&::before {
													background-image: url(../images/svg/value-think.svg);
												}
											}
											&:nth-child(2) {
												&::before {
													background-image: url(../images/svg/value-focus.svg);
												}
											}
											&:nth-child(3) {
												&::before {
													background-image: url(../images/svg/value-power.svg);
												}
											}
											&:nth-child(4) {
												&::before {
													background-image: url(../images/svg/value-target.svg);
												}
											}
										}
									}
								}
							}
						}
					}

					.subsection.module-a.ceo {
						@include section-padding(top);
						@include section-padding(bottom);

						.subsection-wrap {
							.subsection-body {
								.info-board {
									display: flex;
									align-items: center;
									flex-direction: column;
									margin: 48rem 0 0 0;
									padding: 0;
									
									@include respond-above(large-1x) {
										flex-direction: row;
										justify-content: space-between;
										align-items: flex-start;
										column-gap: 32rem;
									}

									&::before {
										content: '';
										display: block;
										display: flex;
										flex: 1 1 100%;
										width: 100%;
										max-width: 547rem;
										aspect-ratio: 1/1;
										background-image: url(../images/about/management_01.png);
										background-repeat: no-repeat;
										background-position: bottom;
										background-size: cover;
										border-radius: 50%;
									}
									
									.board-wrap {
										flex: 1 1 100%;
										display: flex;
										flex-direction: column;
										// row-gap: 40rem;
										
										
										@include respond-below(large-1x) {
											margin-top: 32rem;
										}
										
										@include respond-above(large-1x) {
											max-width: 600rem;
											// row-gap: 56rem;
										}

										.board-head {
											.board-subject {
												margin: 0;
												@include font((outline: body, size: small, weight: extra-light)...);
												color: color(normal, 010);

												@include respond-above(large-1x) {
													@include font((outline: head, size: large)...);
												 }

												.wbr {
													display: block;
													.em {
														font-weight: 600;
													}
												}
											}
										}
										.board-body {
											display: flex;
											flex-direction: column;
											row-gap: 16rem;
											margin: 40rem 0 0 0;
											padding: 0;

											&::before {
												content: '';
												display: block;
												width: 20rem;
												height: 2rem;
												background-color: color(accent, 010);
											}

											@include respond-above(large-1x) {
												row-gap: 24rem;
												margin: 56rem 0 0 0;
											 }

											.para {
												@include font((outline: body, size: small-1x)...);
												color: color(normal, 020);

												@include respond-above(large-1x) {
													@include font((outline: body, size: small)...);

													.wbr {
														display: block;
													}
												 }
											}
										}
										.board-side {
											display: flex;
											column-gap: 8rem;
											align-items: flex-end;
											margin: 26rem 0 0 0;
											@include font((outline: body, size: small-2x)...);
											color: color(normal, 030);

											@include respond-above(large-1x) {
												column-gap: 20rem;
												margin: 32rem 0 0 0;
												@include font((outline: body, size: small-1x)...);
											 }

											.em {
												font-style: normal;
												@include font((outline: body, size: small-1x)...);
												color: color(normal, 010);

												@include respond-above(large-1x) {
													@include font((outline: head, size: large)...);
												 }
											}

											&::after {
												content: '';
												display: block;
												width: 24rem;
												height: 24rem;
												background-image: url(./../images/about/management_02.png);
												background-repeat: no-repeat;
												background-position: center;
												background-size: 22rem 19rem;

												
												@include respond-above(large-1x) {
													width: 43rem;
													height: 36rem;
													background-size: cover;
												 }
											}
										}
									}
								}
							}
						}
					}
				}
			}
		}

		.section.history {
			position: relative;
			@include section-padding(top);
			@include section-padding(bottom);

			&::before {
				content: '';
				display: block;
				position: absolute;
				width: 100vw;
				left: calc(50% - 50vw);
				top: 0;
				bottom: 0;
				right: 0;
				background-color: color(base, 005);
				z-index: -1;
			}

			 .section-wrap {
				.section-body {
					>.data-display {
						>.data-list {
							display: flex;
							flex-direction: column;
							row-gap: 64rem;
					
							@include respond-above(large-1x) {
								row-gap: 80rem;
							}
					
							>.data-item {
								>.data-wrap {
									position: relative;
									display: flex;
									flex-direction: column;
									row-gap: 32rem;
					
									>.data-head {
										display: flex;
										flex-direction: column;
										row-gap: 4rem;
					
										@include respond-above(large-1x) {
											row-gap: 8rem;
										}
					
										>.data-subject {
											margin: 0;
											@include font((outline: body, size: small-2x, weight: semi-bold)...);
											color: color(accent, 010);
					
											@include respond-above(large-1x) {	
												@include font((outline: body, size: small-1x)...);
											}
					
											.data-name {
												display: block;
											}
										}
										>.data-summary {
											margin: 0;
											@include font((outline: body, size: small, weight: semi-bold)...);
											color: color(normal, 010);
					
											@include respond-above(large-1x) {	
												@include font((outline: body, size: large-2x)...);
											}
										}
									}
					
									>.data-body {
										>.data-display {
											display: flex;
											flex-direction: column;
											row-gap: 32rem;
					
					
											@include respond-above(large-1x) {
												row-gap: 64rem;
											 }
					
											+.data-display {
												margin-top: 64rem;
					
												@include respond-above(large-1x) {
													margin-top: 80rem;
												 }
											}
											
					
											>.data-list {
												display: flex;
												flex-direction: column;
										
												>.data-item {
													display: flex;
													flex-direction: column;
					
													&::after,
													&::before {
														display: block;
														width: 100%;
														max-width: 586rem;
														aspect-ratio: 1 / 0.768;
														background-repeat: no-repeat;
														background-position: center;
														background-size: cover;
														border-radius: 20rem;
													}
					
													&:not(.accent) {
														>.data-wrap {
															>.data-head {
																&::before {																	
																	clip-path: circle(25%);
																	background-color: color(accent, 010);
																}
															}
														}
													}
					
													&:not(:last-child) {
														>.data-wrap {
															padding-bottom: 32rem;
					
															
					
															@include respond-above(large-1x) {
																padding-bottom: 96rem;
															}
														}
													}
										
													>.data-wrap {
														display: flex;
														flex-direction: column;
														align-items: stretch;
														row-gap: 8rem;
														position: relative;
					
														
														&::before {
															content: '';
															display: block;
															width: 1rem;
															position: absolute;
															top: 12rem;
															left: 8rem;
															bottom: -12rem;
															z-index: -1;
															background-color: color(base, 020);
														}
					
														@include respond-above(large-1x) {
															row-gap: 24rem;
					
															&::before {
																top: 18rem;
																bottom: -18rem;
																left: 15rem;
															}
														}
					
														>.data-head {
															display: flex;
															align-items: center;
															column-gap: 16rem;
					
															@include respond-above(large-1x) {
																column-gap: 48rem;
															}
															
															&::before {
																content: '';
																display: block;
																width: 16rem;
																height: 16rem;
																box-sizing: border-box;
																border-radius: 50%;
																background-color: color(normal, 000);
																border: 3rem solid color(accent, 010);
					
																@include respond-above(large-1x) {
																	width: 30rem;
																	height: 30rem;
																	border-width: 5rem;
																}
															}
					
															>.data-name {
																display: block;
																@include font((outline: body, size: small-1x, weight: semi-bold)...);
																color: color(normal, 010);
					
																@include respond-above(large-1x) {
																	@include font((outline: head, size: large)...);
																 }
										
															}
														}
					
														>.data-body {
															padding-left: 32rem;
															@include font((outline: body, size: small-2x)...);
															color: color(normal, 010);
					
															>.data-list {
																display: flex;
																flex-direction: column;
																row-gap: 8rem;
															}
															
															@include respond-above(large-1x) {
																padding-left: 78rem;
																@include font((outline: body, size: small-1x)...);
					
																.data-list {
																	row-gap: 16rem;
																}
															}
														}
													}
												}
											}
										}
									}
									
									@include respond-above(large-1x) {
										flex-direction: row;
										justify-content: space-between;
					
										>.data-body {
											width: 45.8%;
											max-width: 586rem;
										}
									}
								}
					
								// 기술과 혁신
								&:nth-child(1) {
									>.data-wrap {
										>.data-body {
											>.data-display {
												>.data-list {
													>.data-item:nth-child(1) {
														&::before {
															content: '';
															margin-bottom: 32rem;
															background-image: url(../images/about/history_01.png);
					
															@include respond-above(large-1x) {
																margin-bottom: 64rem;
															}
														}
													}
					
													>.data-item:nth-child(2) {
														@include respond-above(large-1x) {
															&::before {
																content: '';
																position: absolute;
																left: 0;
																bottom: 0;
																width: 45.8%;
																background-image: url(../images/about/history_02.png);
															}
														}
													}
												}
											}
										}
									}
								}
					
								// 지속적인 성장
								&:nth-child(2) {
									>.data-wrap {
										>.data-body {
											>.data-display {
												>.data-list {
													>.data-item:nth-child(1) {
														&::before {
															content: '';
															margin-bottom: 32rem;
															background-image: url(../images/about/history_03.png);
					
															@include respond-above(large-1x) {
																margin-bottom: 64rem;
															}
														}
													}
					
													>.data-item:nth-child(3) {
														>.data-wrap {
															padding-bottom: 0;
														}
													}
					
													>.data-item:nth-child(4) {
														margin-top: 64rem;
					
														&::before {
															content: '';
															margin-bottom: 32rem;
															background-image: url(../images/about/history_05.png);
															
															@include respond-above(large-1x) {
																margin-bottom: 64rem;
															}
														}
														
														
														@include respond-above(large-1x) {
															margin-top: 108rem;
					
															&::after {
																content: '';
																position: absolute;
																left: 0;
																width: 45.8%;
																transform: translateY(calc(-100% - 108rem));
																background-image: url(../images/about/history_04.png);
															}
														}
													}
												}
											}
										}
									}
								}
							}
						}
					}
				}
			 }
		}

		.section.partners {
			@include section-padding(top);
			@include section-padding(bottom);
		}
	}
 }