@import 'partials/_variables';
@import 'partials/_mixin';

/**
 * @type page
 * @name page-request
**/
.page-body.page-request {
	.content-body {
		.section.module-a {
			@include section-padding(top);
			@include section-padding(bottom);
	
			.subsection.project-inquiry,
			.subsection.basic-information{
				.subsection-wrap {
					display: flex;
					flex-direction: column;
					row-gap: 8rem;
	
					 // laptop
					 @include respond-above(large-1x) {
						row-gap: 16rem;
					}
	
					.subsection-head {
						.subsection-subject {
							margin: 0;
							@include font((outline: body, size: small, weight: semi-bold)...);
							color: color(normal, 010);
	
							// laptop
							@include respond-above(large-1x) {
								@include font((outline: head, size: large)...);
							}
							
							.subsection-name {
								display: block;
							}
						}
					}
				}
			}
	
			.subsection.project-inquiry {
				.subsection-body {
					.submit-form.module-a {
						// laptop
						@include respond-above(large-1x) {
							.form-list {
								display: flex;
								flex-wrap: wrap;
								flex-direction: row;
								column-gap: 32rem;
		
								.form-item {
									&:nth-child(1),
									&:nth-child(2),
									&:nth-child(3) {
										width: calc((100% / 3) - 22rem);
									}  
		
									&:nth-child(4),
									&:nth-child(5) {
										flex: 1 1 100%;
									}
								}
							}
						}
					}
				}
			}
	
			.subsection.basic-information {
				margin-top: 64rem;
	
				// laptop
				@include respond-above(large-1x) {
					margin-top: 80rem;
				}
	
				.submit-form.module-a {
					.form-list {
						.form-item {
							.form-wrap {
								.form-body {
									.form-area {
										.form {
											// mobile
											@include respond-below(large-1x) {
												flex:  1 1 100%;
											}
											// laptop
											@include respond-above(large-1x) {
												width: 640rem;
											}
										}
									}
								}
							}
						}
					}
				}
			}
	
			.para {
				margin-top: 8rem;
				@include font((outline: body, size: small-1x)...);
				color: color(normal, 030);
	
				a {
					color: color(normal, 030);
				}			
	
				// laptop
				@include respond-above(large-1x) {
					margin-top: 16rem;
				}
			}

			.section-util {
				margin-top: 48rem;
			}
		}
	}
}