@import 'partials/_variables';
@import 'partials/_mixin';

/**
 * @type page
 * @name page-people
**/
.page-body.page-people {
	#local {
		.local-head {
			&::before {
				background-image: url(../images/people/main_bg_01.png);
			}
		}
	}

	.content-body {
		// company-culture
		.section.company-culture {
			@include section-padding(top);

			.section-wrap {
				.section-body {
					.subsection {
						@include section-padding(top);
						@include section-padding(bottom);
						@include section-margin(top);
					}

					.data-display.company-culture {
						--item-size: 260rem;
		
						.data-list {
							display: flex;
							flex-direction: column;
							row-gap: 48rem;
		
							.data-item {
								display: flex;
								flex-direction: column;
								align-items: center;
								row-gap: 16rem;

								&::before {
									content: '';
									display: block;
									width: 96rem;
									aspect-ratio: 1 / 1;
									background-repeat: no-repeat;
									background-position: center;
									background-size: cover;
								}
		
								.data-wrap {
									display: flex;
									flex-direction: column;
									justify-content: center;
									align-items: center;
									position: relative;
									text-align: center;
									
									.data-head {
										display: flex;
										flex-direction: column;
										row-gap: 8rem;
		
										.data-subject {
											margin: 0;
											@include font((outline: head, size: small, weight: semi-bold)...);
											color: color(normal, 010);
			
											.data-name {
												display: block;
											}
										}
		
										.data-summary {
											margin: 0;
											@include font((outline: body, size: small-1x)...);
											color: color(normal, 020);
			
											.wbr {
												display: block;
											}
										}
									}
								}
								
								&:nth-child(1) {
									&::before {
										background-image: url(./../images/svg/culture-trophy.svg);
									}
								}
		
								&:nth-child(2) {
									&::before {
										background-image: url(./../images/svg/culture-schedule.svg);
									}
								}
		
								&:nth-child(3) {
									&::before {
										background-image: url(./../images/svg/culture-hand-shake.svg);
									}
								}
							}
		
							// laptop
							@include respond-above(large-1x) {
								justify-content: space-between;
								flex-direction: row;
								position: relative;
		
								.data-item {
									width: 33.3%;
		
									&::before {
										position: absolute;
										top: 0;
										width: var(--item-size);
										border: 1rem solid color(accent, 010);
										background-size: 160rem;
										border-radius: 50%;
										box-sizing: border-box;
										outline: 1rem dashed color(primary, 030);
										outline-offset: 25rem;
									}
		
									// line
									&:nth-child(1) {
										&::after {
											content: '';
											display: block;
											position: absolute;
											z-index: 1;
											top: calc((var(--item-size) / 2) - 8rem);
											left: calc(33.3333% - ((33.3333% - var(--item-size)) / 2));
											width: calc(33.3333% - var(--item-size));
											height: 17rem;
											box-shadow: inset 0 8rem 0 0 color(base, 000), inset 0 -8rem 0 0 color(base, 000);
											background-color: color(accent, 010);
										}
									}
		
									// line
									&:nth-child(2) {
										&::after {
											content: '';
											display: block;
											position: absolute;
											z-index: 1;
											top: calc((var(--item-size) / 2) - 8rem);
											left: calc(66.6666% - ((33.3333% - var(--item-size)) / 2));
											width: calc(33.6666% - var(--item-size));
											height: 17rem;
											box-shadow: inset 0 8rem 0 0 color(base, 000), inset 0 -8rem 0 0 color(base, 000);
											background-color: color(accent, 010);
										}
									}
		
									// point
									&:nth-child(1) {
										.data-wrap {
											&::after {
												content: '';
												right: -6rem;
												background-color: color(accent, 010);
											}
										}
									}
		
									// point
									&:nth-child(2) {
										.data-wrap {
											&::before {
												content: '';
												left: -6rem;
												background-color: color(base, 000);
											}
		
											&::after {
												content: '';
												right: -6rem;
												background-color: color(base, 000);
											}
										}
									}
		
									// point
									&:nth-child(3) {
										.data-wrap {
											&::before {
												content: '';
												left: -6rem;
												background-color: color(accent, 010);
											}
										}
									}
		
									.data-wrap {
										width: var(--item-size);
										padding-top: calc(var(--item-size) + 32rem + 25rem);
		
										&::after,
										&::before {
											display: block;
											position: absolute;
											z-index: 2;
											top: calc((var(--item-size) / 2) - 6rem);
											width: 12rem;
											height: 12rem;
											border-radius: 50%;
											box-sizing: border-box;
											border: 1rem solid color(accent, 010);
											box-shadow: 0 0 8rem 0 color(primary, 030), 0 8rem 0 color(base, 000);
										}
		
										.data-head {
											row-gap: 16rem;
		
											.data-summary {
												.wbr {
													white-space: nowrap;
												}
											}
										}
									}
								}
							}
						}
					}

					.data-display.module-a.type-c.style-c {
						.data-list {
							display: flex;
							flex-direction: row;
							flex-wrap: wrap;
							gap: 8rem;

							.data-item {
								width: calc((100% / 2) - 4rem);
								
								&.flexible-work {
									&::before {
										background-image: url(../images/svg/work-time.svg);
									}
								}
								&.smart-day {
									&::before {
										background-image: url(../images/svg/work-calendar.svg);
									}
								}
								&.refresh-vacation {
									&::before {
										background-image: url(../images/svg/work-plane.svg);
									}
								}
								&.education-support {
									&::before {
										background-image: url(../images/svg/work-book.svg);
									}
								}
								&.childcare-support {
									&::before {
										background-image: url(../images/svg/work-stroller.svg);
									}
								}
								&.condolences-support {
									&::before {
										background-image: url(../images/svg/work-letter.svg);
									}
								}
								&.reward-system {
									&::before {
										background-image: url(../images/svg/work-award.svg);
									}
								}
								&.happy-lunch {
									&::before {
										background-image: url(../images/svg/work-lunch.svg);
									}
								}
								&.happy-birthday {
									&::before {
										background-image: url(../images/svg/work-cake.svg);
									}
								}
							}

							
							// laptop
							@include respond-above(large-1x) {
								gap: 32rem;

								.data-item {
									width: calc((100% / 3) - 22rem);
								}
							 }
						}
					}
				}
			}
		}

		// .section.our-team 
		.section.our-team {
			.section-wrap {
				position: relative;
				z-index: 1;
				@include section-padding(top);
				@include section-padding(bottom);

				&::after {
					content: '';
					display: block;
					position: absolute;
					right: 0;
					bottom: 64rem;
					width: calc(100vw - 20rem);
					height: 192rem;
					// background: url(../../assets/images/people/team_01.png) no-repeat center/cover;
					background-repeat: no-repeat;
					background-position: center;
					background-size: cover;
					background-image: var(--background-image);
					border-top-right-radius: 260rem;
					border-bottom-right-radius: 260rem;
				}

				// mobile
				@include respond-below(large) {
					padding-bottom: calc(192rem + 32rem + 64rem);
				}

				// tablet
				@include respond-above(large) {
					display: flex;
					justify-content: center;
					flex-direction: column;
					min-height: 232rem;
					padding-left: calc(50% + 80rem);

					&::after {
						right: 50%;
						width: 50vw;
						height: 232rem;
					}
				}

				// laptop
				@include respond-above(large-1x) {
					padding-left: calc(50% + 80rem);
					min-height: 520rem;

					&::after {
						height: 520rem;
						bottom: 140rem;
					}
				}
			}

			.section-body {
				.para {
					margin-top: 16rem;

					.wbr {
						// laptop
						@include respond-above(large-1x) {
							display: inline-block;
							vertical-align: top;
						}
					}
				}
			}
		}

		// .section.our-team 
		.section.job-posting {
			.section-wrap {
				.section-head {
					@include hidden;
				}

				.subsection-head {
					margin-bottom: 48rem;
	
					// laptop
					@include respond-above(large-1x) {
						margin-bottom: 80rem;
					}
				}

				.subsection.ideal-talent {
					position: relative;
					@include section-padding(top);
					@include section-padding(bottom);

					&::before {
						content: '';
						display: block;
						position: absolute;
						bottom: 0;
						width: 100vw;
						top: 0;
						right: 0;
						left: calc(50% - 50vw);
						bottom: 0;
						z-index: -1;
						background: linear-gradient(236.04deg, rgba(243, 161, 166, 0.2) 15.19%, rgba(171, 175, 191, 0.1) 79.41%, rgba(67, 81, 109, 0.06) 106.18%, rgba(255, 255, 255, 0) 125.28%), linear-gradient(180deg, rgba(246, 246, 249, 0.5) 0%, rgba(246, 246, 249, 0) 100%);
						opacity: 0.8;
					}

					.subsection-body {
						.data-display.module-b {
							.data-list {
								flex-direction: column;
								row-gap: 32rem;
		
								// laptop
								@include respond-above(large-1x) {
									flex-direction: row;
									flex-wrap: wrap;
									row-gap: 112rem;
		
									.data-item {
										width: 50%;
									}
								}
							}
						}
					}
				}

				.subsection-body {
					.info-board.module-b {
						.board-util {
							.button-display.module-a {
								.button-area {
									.btn {
										background-color: transparent;
									}
								}
							}

							&:hover {
								.button-display.module-a {
									.button-area {
										.btn {
											color: color(normal, 000);
											background-color: color(normal, 010);

											.btn-text {
												&::after {
													background-color: color(normal, 000);
												}
											}
										}
									}
								}
							}
						}
					}
				}

				.subsection.job-posting {
					@include section-padding(top);
				}

				.subsection.recruitment-procedure {
					@include section-padding(top);
					@include section-padding(bottom);
				}
			}
		}	
	}
}