@import 'partials/_variables';
@import 'partials/_mixin';

/**
 * @type page
 * @name page-business
**/
.page-body.page-business {
	#local {
		.local-head {
			&::before {
				background-image: url(../images/business/main_bg_01.png);
			}
		}
	}
	.content-body {
		// room25
		.section.room25 {
			position: relative;
			@include section-padding(top);
			// @include section-padding(bottom);

			.section-wrap {
				&::after {
					content: '';
					display: block;
					position: absolute;
					left: 0;
					bottom: 0;
					width: calc(100vw - 20rem);
					height: 192rem;
					background: url(../../assets/images/business/room25_01.png) no-repeat center/cover;
					border-top-left-radius: 260rem;
					border-bottom-left-radius: 260rem;
				}

				// tablet
				@include respond-below(large) {
					padding-bottom: calc(192rem + 32rem);
				}

				// tablet
				@include respond-above(large) {
					padding-right: calc(50% + 80rem);

					&::after {
						left: 50%;
						width: 50vw;
						height: 232rem;
					}
				}

				// laptop
				@include respond-above(large-1x) {
					padding-right: calc(50% + 80rem);

					&::after {
						height: 520rem;
					}
				}
			}

			.data-display {
				.data-list {
					display: flex;
					flex-direction: column;
					row-gap: 24rem;

					// laptop
					@include respond-above(large-1x) {
						row-gap: 84rem;
					}

					.data-item {
						&:nth-child(1) {
							&::before {
								background-image: url(../images/svg/room25-people.svg);
							}
						}
						&:nth-child(2) {
							&::before {
								background-image: url(../images/svg/room25-nice.svg);
							}
						}
						&:nth-child(3) {
							&::before {
								background-image: url(../images/svg/room25-restaurant.svg);
							}
						}
					}
				}
			}

			.section-util {
				position: absolute;
				z-index: 1;
				left: 66rem;
				bottom: calc((192rem / 2) - 24rem);

				.button-display {
					.button-area {
						.btn {
							color: color(base, 000);
							border-color: color(base, 000);
							background-color: transparent;

							.btn-text {
								&::after,
								&::before {
									background-color: color(base, 000);
								}
							}
						}
					}
				}

				// tablet
				@include respond-above(large) {
					left: calc(50% + 48rem);
					bottom: calc((232rem / 2) - 24rem);
				}

				// laptop
				@include respond-above(large-1x) {
					left: calc(50% + 96rem);
					bottom: calc((520rem / 2) - 24rem);
				}
			}
		}

		// useller
		.section.useller {	
			position: relative;
			@include section-padding(top);

			.section-wrap {
				&::after {
					content: '';
					display: block;
					position: absolute;
					right: 0;
					bottom: 0;
					width: calc(100vw - 20rem);
					height: 192rem;
					background: url(../../assets/images/business/useller_01.png) no-repeat center/cover;
					border-top-right-radius: 260rem;
					border-bottom-right-radius: 260rem;
				}

				// tablet
				@include respond-below(large) {
					padding-bottom: calc(192rem + 32rem);
				}

				// tablet
				@include respond-above(large) {
					padding-left: calc(50% + 80rem);

					&::after {
						right: 50%;
						width: 50vw;
						height: 232rem;
					}
				}

				// laptop
				@include respond-above(large-1x) {
					padding-left: calc(50% + 80rem);

					&::after {
						height: 520rem;
					}
				}
			}

			.data-display {
				.data-list {
					display: flex;
					flex-direction: column;
					row-gap: 24rem;

					// laptop
					@include respond-above(large-1x) {
						row-gap: 84rem;
					}

					.data-item {
						&:nth-child(1) {
							&::before {
								background-image: url(../images/svg/useller-sns.svg);
							}
						}
						&:nth-child(2) {
							&::before {
								background-image: url(../images/svg/useller-cart.svg);
							}
						}
						&:nth-child(3) {
							&::before {
								background-image: url(../images/svg/useller-check.svg);
							}
						}
					}
				}
			}

			.section-util {
				position: absolute;
				z-index: 1;
				right: 66rem;
				bottom: calc((192rem / 2) - 24rem);

				.button-display {
					.button-area {
						.btn {
							color: color(base, 000);
							border-color: color(base, 000);
							background-color: transparent;

							.btn-text {
								&::after,
								&::before {
									background-color: color(base, 000);
								}
							}
						}
					}
				}

				// tablet
				@include respond-above(large) {
					right: calc(50% + 48rem);
					bottom: calc((232rem / 2) - 24rem);
				}

				// laptop
				@include respond-above(large-1x) {
					right: calc(50% + 96rem);
					bottom: calc((520rem / 2) - 24rem);
				}
			}
		}

		// si/sm 
		.section.si-sm {
			position: relative;
			@include section-padding(top);

			.section-wrap {
				&::after {
					content: '';
					display: block;
					position: relative;
					left: calc(50% - 50vw);
					width: 100vw;
					height: 160rem;
					background: url(../../assets/images/business/contact_01.png) no-repeat center/cover;

					// laptop
					@include respond-above(large-1x) {
						height: 380rem;
					}
				}

				.section-head {
					margin-bottom: 16rem;

					// laptop
					@include respond-above(large-1x) {
						margin-bottom: 20rem;
					}
				}

				.section-util {
					position: absolute;
					z-index: 1;
					left: 50%;
					bottom: calc((160rem / 2) - 24rem);
					transform: translateX(-50%);

					.button-display {
						.button-area {
							.btn {
								color: color(base, 000);
								border-color: color(base, 000);
								background-color: transparent;

								.btn-text {
									&::after,
									&::before {
										background-color: color(base, 000);
									}
								}
							}
						}
					}

					// tablet
					@include respond-above(large) {
						bottom: calc((160rem / 2) - 24rem);
					}

					// laptop
					@include respond-above(large-1x) {
						bottom: calc((380rem / 2) - 24rem);
					}
				}
			}
		}

		// portfolio 
		.section.portfolio {
			position: relative;
			@include section-padding(top);
			@include section-padding(bottom);

			.section-wrap {
				display: flex;
				flex-direction: column;

				.section-head {
					order: 1;
					margin-bottom: 16rem;

					// laptop
					@include respond-above(large-1x) {
						margin-bottom: 24rem;
					}


				}
				.section-body {
					order: 3;

					.data-display.module-c {
						>.data-list {
							display: flex;
							padding-bottom: 86rem;

							// laptop
							@include respond-above(large-1x) {
								padding-bottom: 138rem;
							}

							>.data-item {
								width: 210rem;

								// laptop
								@include respond-above(large-1x) {
									width: 302rem;
								}
							}
						}

						.swiper-button-prev,
						.swiper-button-next {
							position: absolute;
							bottom: 18rem;
							z-index: 3;
							width: 28rem;
							height: 28rem;
							background-repeat: no-repeat;
							background-position: center;
							background-size: cover;

							// laptop
							@include respond-above(large-1x) {
								width: 40rem;
								height: 40rem;
							}
						}

						.swiper-button-prev {
							left: 0;
							background-image: icon(arrow-left, color(normal, 010));
						}

						.swiper-button-next {
							right: 0;
							background-image: icon(arrow-right, color(normal, 010));
						}

						.swiper-scrollbar {
							position: absolute;
							bottom: 0;
							left: 0;
							width: 100%;
							height: 2rem;
							z-index: 3;
							
							.swiper-scrollbar-drag {
								background-color: color(normal, 010);
							}
						}
					}
				}
				.section-util {
					order: 2;
					margin-bottom: 48rem;

					// laptop
					@include respond-above(large-1x) {
						margin-bottom: 80rem;
					}
				}
			}
		}

	}
}


/**
 * @type page
 * @name page-portfolio-list
**/
.page-body.page-business.page-portfolio-list {
	.local-head {
		@include hidden;
	}
	.content-body {
		.section.module-a {
			@include section-padding(top);
			@include section-padding(bottom);

			.section-body {
				.data-display.module-c {
					>.data-list {
						display: flex;
						flex-direction: column;
						row-gap: 48rem;
						align-items: center;

						>.data-item {
							width: 100%;
							max-width: 288rem;
						}

						// laptop
						@include respond-above(large-1x) {
							flex-direction: row;
							flex-wrap: wrap;
							row-gap: 56rem;
							column-gap: 32rem;

							>.data-item {
								max-width: calc(33.33% - (64rem / 3));

								>.data-wrap {
									max-width: 100%;
								}
							}

						}
					}
				}
			}
		}
	}
}


/**
 * @type page
 * @name page-portfolio-view
**/
.page-body.page-business.page-portfolio-view {
	.content-body {
		.section {
			.section-wrap {
				.section-head {
					@include hidden;
				}
				.section-body {
					.posts-read {
						@include section-padding(top);
					}
				}
			}
		}
	}
 }